import server from '../../server.js';
import md5 from '../../static/md5.js';
export default {
	data() {
		return {
			xzId:1,
			showVip:false,
			gaodu:'-710px',
			yanse:'rgba(0,0,0,0)',
			userInfo:[],
			monnumber:1,
			money:0,
			endVipTime:'开通安个利VIP,畅享高级功能',
			ann:false,
			onemonn:10,
			isVip:false
		}
	},
	onLoad:function(){
		this.chushihua();
	},
	methods: {
		chushihua:function(){
			this.userInfo=server.userinfo
			if(this.userInfo.VIPEndTime>0){
				this.endVipTime="你已成为安个利VIP，还有"+parseInt(this.userInfo.VIPEndTime)+"天到期";
				this.ann=true
				this.isVip=true
			}
			console.log(this.userInfo)
			this.cxjg();
		},
		getxieyi:function(){
			uni.navigateTo({
				url:"../edit/edit?type=6"
			})
		},
		cxjg:function(){
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/user.php?type=getisvip', 
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					this.onemonn=res.data.data	
				}
			});
		},
		getVip:function(){
			if(this.monnumber>=1){
				if(this.monnumber==1){
					this.money=this.onemonn
				}else{
					this.money=this.monnumber*20-this.monnumber*4
				}
				console.log('月份：'+this.monnumber,'金额：'+this.money)
				uni.showLoading({
					title: '加载中'
				});
				let wxkey=md5('不停'+String(Date.now()));
				uni.request({
					method:'POST',
					url: "https://api.angeli.top/WeChat/pay.php?type=vip", //仅为示例，并非真实接口地址。
					data: {
						openid:server.userinfo.wxOpenId,
						fee:this.money,
						number:this.monnumber,
						key:wxkey,
						auid:server.userinfo.Auid
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
						'Cookie':server.cookie
					},
					success: (res) => {
						console.log(res)
						let dd=res.data.orderId;
						if(res.data.code=="1"){
							let sjstr=md5(String(Date.now()));
							let time=String(Date.now())
							let signTemp="appId=wxb2418420ae2cf37c&nonceStr="+sjstr+"&package=prepay_id="+res.data.data.prepay_id+"&signType=MD5&timeStamp="+time+"&key=xinfenghuliankejiyouxiangongsi12"
							let sign=md5(signTemp)
							uni.requestPayment({
								provider: 'wxpay',
								timeStamp: time,
								nonceStr: sjstr,
								package: 'prepay_id='+res.data.data.prepay_id,
								signType: 'MD5',
								paySign: sign,
								success: (res) => {
									uni.showLoading({
										title: '正在查询中...'
									});
									uni.request({
										method:'GET',
										url: 'https://api.angeli.top/account.php?type=queryOrderIdAndVip', 
										data:{
											auid:server.userinfo.Auid,
											orderId:dd
										},
										header: {
											'content-type': 'application/x-www-form-urlencoded',
											'Cookie':server.cookie
										},
										success: (res) => {
											if(res.data.data.payStatus=='已支付' || res.data.data.payStatus=='OK'){
												let endtime=res.data.data.userInfo.VIPEndTime;
												this.endVipTime="你已成为安个利VIP，还有"+parseInt(endtime)+"天到期";
												console.log('已支付',this.endVipTime)
												this.showVip=false;
												this.ann=true
												uni.showToast({
													title: "已开通VIP",
													position:'bottom',
													icon:'none',
													position:'center'
												})
												this.$getUserinfo();
												this.$forceUpdate();
												//uni.navigateBack()
											}else{
												uni.showToast({
													title: "支付失败！请联系客服",
													position:'bottom',
													icon:'none',
													position:'center'
												})
											}
											this.chushihua();
										},
										complete: () => {
											uni.hideLoading();
										}
									});
								},
								fail: function (err) {
									uni.showToast({
										title: "支付失败！请联系客服",
										position:'bottom',
										icon:'none',
										position:'center'
									})
								}
							});
							
						}else{
				
						}
						
					},
					complete() {
						uni.hideLoading();
					}
				});
				
			}else{
				uni.showToast({
					title: "必须选择月份",
					position:'bottom',
					icon:'none',
					position:'center'
				})
			}
		},
		showKaitong:function(){
			this.showVip=true
			this.gaodu='0px'
			this.yanse='rgba(0,0,0,0.4)'
		},
		inputVip:function(e){
			this.monnumber=e.target.value
		},
		hideVip:function(){
			this.showVip=false
			this.gaodu='-710px';
			this.yanse='rgba(0,0,0,0)'
			
		},
		xuanze:function(e){
			switch (e){
				case 1:
					this.monnumber=1
					break;
				case 2:
					this.monnumber=3
					break;
				case 3:
					this.monnumber=12
					break;
				case 4:
					this.monnumber=12
					break;
				default:
					this.monnumber=0
					break;
			}
			this.xzId=e
			/* 新思路,点赞改变列表状态可以使用该方法,:class="当前ID是否等于列子ID，如果是那么就点赞，如果不是就没有点赞，"
			但是,这样只能存在一个点赞状态,其他的状态就会消失,带思考 */
		}
	}
}
